<template>
  <div>
    <div class="row mb-4">
      <div class="col">
        <h3 class="fw-bold">Pages</h3>
      </div>
    </div>
    <template>
      <v-form ref="form" v-model="valid" lazy-validation>

        <v-autocomplete label="Page Type" v-model="pageType" :items="pageDetails" item-text="page_name" item-value="id" autocomplete="on" @change="getPageDetail"></v-autocomplete>

        <ckeditor v-model="pagecontent.content"></ckeditor>

        <!-- <p class="font-weight-black">Meta Details</p> -->

        <!-- <v-text-field label="Meta Title" required></v-text-field>

        <v-text-field label="Meta Keywords" required></v-text-field>

        <v-textarea label="Meta Description"></v-textarea> -->

        <v-btn v-if="isLoading" class="cred">Loading...</v-btn>
        <v-btn v-else  class="cred" @click="updatePageDetails"> SAVE</v-btn>
      </v-form>
      <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
        <template v-slot:action="{attrs}">
          <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
        </template>
      </v-snackbar>
    </template>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "PageContent",
  components: {  },
  data (){
     return{
      pageDetails:[],
      pagecontent:{},
      snackbar: false,
      text: '',
      timeout: '',
      isLoading:false,
     }  
    },
    created (){
      this.index();
    },
    methods:{

      index() {
        axios.get("get/pages").then( (resp)=> {
          this.pageDetails = resp.data.pages
        });
      },
      getPageDetail(){
        var data = {
          id : this.pageType,
        };
        axios.post("page/content",data).then( (resp)=> {
          this.pagecontent = resp.data.content;
        });
      },

      updatePageDetails(){
        this.isLoading = true;
        var data = {
          id : this.pageType,
          content : this.pagecontent.content
        };
        axios.post("update/page/content",data).then( (resp)=> {
          this.pagecontent = resp.data.content;
          this.snackbar = true;
          this.isLoading = false,
          this.text = resp.data.message;
          this.timeout = 3000;
          this.getPageDetail();
        });
      },
    }
};
</script>
